import './App.css';
import { useEffect, useRef, useState } from 'react';
import { detection } from './utils/faceShapeProcessor';
import { FilesetResolver, FaceLandmarker } from '@mediapipe/tasks-vision';
import Webcam from 'react-webcam';
import { base64_to_img } from './utils/commonFunctions';
import { drawSquareCorners } from './utils/drawingCanvas'
import { FaceMesh } from "@mediapipe/face_mesh";
import { depthProcessor, depthProcessorEnd } from './utils/depthProcessor';




function App() {


  const PATH_MODEL = 'https://storage.googleapis.com/mediapipe-models/face_landmarker/face_landmarker/float16/1/face_landmarker.task'
  const runningMode = "IMAGE"


  const textRefLength = useRef(null)
  const imgRef = useRef(null)
  const buttonRef = useRef(null)
  const canvasRef = useRef(null)
  const canvasRefLive = useRef(null)
  const textRefJawline = useRef(null)
  const textRefResult = useRef(null)
  const webcamRef = useRef(null)
  const textRefColor = useRef(null)
  const textRefOutput = useRef(null)
  const resizeCanvasRef = useRef(null)
  const textRefDistance = useRef(null)
  const canvasInfoRef = useRef(null);


  const [progress, setProgress] = useState(0)
  const [webcamOpen, setWebcamOpen] = useState(false)
  const [disabled, setDisabled] = useState(true)
  const [camera, setCamera] = useState('environment')
  const [loaded, setLoaded] = useState(false)
  const [imgBase64, setImgBase64] = useState(null);
  const [imgSrc, setImgSrc] = useState(null);
  const [modelLandmarker, setModelLandmarker] = useState(null);
  const [modelDepth, setModelDepth] = useState(null);
  const [result, setResult] = useState("Detecting");
  const [PDValue, setPDValue] = useState("");
  const [distance, setDistance] = useState(null);
  const [lastDistance, setLastDistance] = useState(null);


  const updateImgBase64 = (data) => {
    setImgBase64(data)
  }
  const updateDistance = (data) => {
    setDistance(data)
  }
  const updatePdValue = (value) => {
    setPDValue(value)
  }
  const updateImgSrc = (data) => {
    setImgSrc(data)
  }
  const updateResult = (data) => {
    setResult(data)
  }

  useEffect(() => {

    if (webcamOpen) {
      depthProcessor(modelDepth, webcamRef, canvasRefLive, updatePdValue, camera, updateDistance)

    }

    else {
      const ctx = canvasRef.current.getContext("2d");
      ctx.clearRect(0, 0, canvasRef.current.width, canvasRef.current.width);
      textRefLength.current.innerText = 'Loading...'
      textRefOutput.current.innerText = 'The Console.log:'
      textRefJawline.current.innerText = ''
      textRefResult.current.innerText = ''
      textRefColor.current.innerText = ''
      readyApp()



    }



  }, [imgSrc, camera, webcamOpen])

  const readyApp = async () => {
    const face_landmarker = await loadModelLandmarker();
    const depth_estimator = await loadDepthEstimator();


    setLoaded(true)
    setModelLandmarker(face_landmarker)
    setModelDepth(depth_estimator)
    setDisabled(false)
    document.getElementById('loading').innerText = 'Open Camera'

  }

  const loadModelLandmarker = async () => {
    const vision = await FilesetResolver.forVisionTasks(
      // path/to/wasm/root
      "https://cdn.jsdelivr.net/npm/@mediapipe/tasks-vision@latest/wasm"
    );
    const model = await FaceLandmarker.createFromOptions(
      vision,
      {
        baseOptions: {
          modelAssetPath: PATH_MODEL
        },
        runningMode: runningMode
      });

    if (model) {
      textRefLength.current.innerText = 'Successful!'
    }
    return model;

  }

  async function loadDepthEstimator() {
    const model = new FaceMesh({
      locateFile: (file) => {
        return `https://cdn.jsdelivr.net/npm/@mediapipe/face_mesh/${file}`;
      },
    });
    await model.initialize()
    return model
  }


  const resizeImage = () => {
    const w = imgRef.current.offsetWidth
    const h = imgRef.current.offsetHeight

    // console.log(imgRef.current.offsetWidth)
    canvasRef.current.width = w
    canvasRef.current.height = h
    resizeCanvasRef.current.width = w
    resizeCanvasRef.current.height = h

    // Determine whether the image is wider or taller
    const isLandscape = w > h;

    const canvasSize = Math.min(w, h);

    resizeCanvasRef.current.width = canvasSize
    resizeCanvasRef.current.height = canvasSize

    const ctx = resizeCanvasRef.current.getContext('2d');

    ctx.fillStyle = 'black';
    ctx.fillRect(0, 0, resizeCanvasRef.current.width, resizeCanvasRef.current.height);

    // Calculate the position to draw the image
    const drawX = isLandscape ? (canvasSize - w) / 2 : 0;
    const drawY = isLandscape ? 0 : (canvasSize - h) / 2;

    // Draw the image on the canvas
    ctx.drawImage(imgRef.current, drawX, drawY, w, h);

    imgRef.current.classList.add('invisible')
  }



  const predictFaceShape = async () => {

    resizeImage()
    textRefOutput.current.innerText += '\n image width: ' + imgRef.current.offsetWidth + '\n imageheight: ' + imgRef.current.offsetHeight
    textRefOutput.current.innerText += '\n canvasRef width: ' + canvasRef.current.offsetWidth + '\n canvasRef height: ' + canvasRef.current.offsetHeight

    detection(modelLandmarker, resizeCanvasRef, canvasRef, textRefJawline, textRefResult, textRefLength, textRefColor, textRefOutput)

  }

  function openCameraHandler() {

    if (!webcamOpen) {



      document.getElementById('webcam').classList.remove('invisible')
      canvasInfoRef.current.classList.remove('invisible')
      canvasRefLive.current.classList.remove('invisible')
      document.getElementById('loading').innerText = 'Capture'
      document.getElementById('switch').classList.remove('d-none')
      textRefDistance.current.classList.remove('d-none')

      const canvasElement = canvasInfoRef.current
      const context = canvasElement.getContext('2d');

      const canvasElementLive = canvasRefLive.current
      const contextLive = canvasElementLive.getContext('2d');


      const videoElement = webcamRef.current.video
      videoElement.addEventListener('loadeddata', handleUserMedia);

      if (videoElement) {

        const aspectRatio = 1; // 1:1 aspect ratio for square video
        const maxWidth = 550; // Max width you want for the video
        const newWidth = Math.min(videoElement.offsetWidth, maxWidth);
        const newHeight = newWidth * aspectRatio;

        videoElement.style.width = `${newWidth}px`;
        videoElement.style.height = `${newHeight}px`;
      }

      if (canvasElement) {

        context.canvas.height = videoElement.offsetHeight
        context.canvas.width = videoElement.offsetWidth

        contextLive.canvas.height = videoElement.offsetHeight
        contextLive.canvas.width = videoElement.offsetWidth

        imgRef.width = videoElement.offsetWidth
        imgRef.height = videoElement.offsetHeight

        drawSquareCorners(canvasInfoRef)
      }

      setWebcamOpen(true)





    }
    else if (webcamOpen) {
      const screenshot = webcamRef.current.getScreenshot()
      updateImgBase64(screenshot)
      const imageElement = base64_to_img(screenshot)
      updateImgSrc(imageElement.src)
      webcamRef.current.video.pause();
      webcamRef.current.video.srcObject = null;
      document.getElementById('webcam').classList.add('d-none')
      document.getElementById('infoCanvas').classList.add('d-none')
      document.getElementById('liveCanvas').classList.add('d-none')
      document.getElementById('switch').classList.add('d-none')
      setDisabled(true)
      document.getElementById('image').classList.remove('d-none')
      canvasRef.current.classList.remove('d-none')
      canvasRef.current.width = imgRef.current.offsetWidth;
      canvasRef.current.height = imgRef.current.offsetHeight

      document.getElementById('logs').classList.remove('d-none')
      setLastDistance(distance)
      textRefDistance.current.classList.add('d-none')
    }

  }

  const handleUserMedia = () => {

    document.getElementById('loading').innerText = 'Capture'
    setDisabled(false)
    document.getElementById('switch').classList.remove('d-none')
    document.getElementById('infoCanvas').classList.remove('invisible')
    document.getElementById('liveCanvas').classList.remove('invisible')




  }

  const cameraViewHandler = () => {

    document.getElementById('loading').innerText = 'Loading Camera'
    document.getElementById('switch').classList.add('d-none')
    setDisabled(true)


    if (camera === 'environment') {
      setCamera('user')
    }

    else {
      setCamera('environment')
    }
    depthProcessorEnd()

  }



  return (
    <>
      <div className="container my-5">

        <center>
          <h1>Faceshape Detection</h1>
          <div >
            <div
              className='button r d-none'
              id="switch">
              <input onClick={cameraViewHandler} type="checkbox" className="checkbox" />
              <div className="knobs"></div>
              <div className="layer"></div>
            </div>
          </div>
          <button
            disabled={disabled}
            id='loading'
            className="btn btn-primary fs-3 my-3"
            onClick={openCameraHandler}
          >
            Loading
          </button>

          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <div style={{ position: 'relative' }}>



              <Webcam
                id="webcam"
                ref={webcamRef}
                screenshotFormat="image/jpeg"
                className="invisible"
                mirrored={camera === 'user'} style={
                  {

                    maxWidth: '550px',
                    width: '100%',
                    objectFit: 'fill'
                    // height: `${document.getElementById('webcam').offsetWidth}`,
                    // height: '100%',
                    // maxHeight: '550px'

                  }

                }
                videoConstraints={{
                  facingMode: camera
                }}
              />





              <canvas
                ref={canvasInfoRef}
                id="infoCanvas"
                className="invisible"
                style={{
                  position: 'absolute',
                  zIndex: '90',
                  left: 0,
                  width: '100%',
                  // height: '100%',
                }}
              ></canvas>

              <canvas
                ref={canvasRefLive}
                id="liveCanvas"
                className="invisible"
                style={{
                  position: 'absolute',
                  zIndex: '90',
                  left: 0,
                  width: '100%',
                  // height: '100%',
                  // width: '300px',
                  // height: '300px',
                }}
              ></canvas>

              <div ref={textRefDistance} className='d-none container bg-dark text-white mt-3  py-2'>
                Distance: {distance}

              </div>




            </div>
          </div>


          <div id='image' className="d-none ">
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <div style={{ position: 'relative' }}>
                <div>

                  <img onLoad={predictFaceShape} ref={imgRef} src={imgBase64} alt="" />

                  <canvas
                    ref={resizeCanvasRef}

                    style={{
                      position: 'absolute',
                      zIndex: '70',
                      left: 0,
                      // width: '100%',
                      // height: '100%',
                    }}
                  ></canvas>

                  <canvas
                    ref={canvasRef}
                    id="output-canvas"
                    className='d-none'
                    style={{
                      position: 'absolute',
                      zIndex: '90',
                      left: 0,
                      // width: '300px',
                      // height: '300px',
                    }}
                  ></canvas>



                </div>

              </div>
            </div>

          </div>

          <div id='logs' className='container d-none'>
            <div className='container bg-dark text-white mt-3  py-2'>
              <span ref={textRefOutput} ></span>

            </div>
            <div className='container bg-info  mt-3  py-2'>
              <span >Distance at time of Capture:{lastDistance}</span>

            </div>
            <div className='container bg-primary text-white mt-3  py-2'>
              <span ref={textRefLength} id="test"></span>

            </div>
            <div className='container bg-danger text-white mt-3  py-2'>

              <span ref={textRefJawline} ></span>

            </div>
            <div className='container bg-success text-white mt-3  py-2'>

              <span ref={textRefResult}></span>
            </div>

            <div className='container bg-warning text-black mt-3  py-2'>

              <span ref={textRefColor}></span>
            </div>
          </div>


        </center>
      </div>
    </>
  );
}

export default App;
